:root {
  --font-color: #293452;
  --main-color: #ffd658;
}
body {
  background-color: var(--font-color);
  color: var(--main-color);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
footer {
  text-align: center;
}
footer a {
  color: var(--main-color);
}
.container {
  max-width: 1200px;
  width: 90%;
  margin: 1rem auto;
}
.title {
  margin-top: 2rem;
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  font-size: 150%;
  text-align: center;
}
.from-control {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.input-control {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.from-control .btn {
  background-color: var(--main-color);
  padding: 0.5rem 1rem;
  border-radius: 5px;
  margin: 1rem;
  color: var(--font-color);
  font-size: 120%;
  font-weight: 600;
  border: none;
  outline: none;
  cursor: pointer;
}

label {
  font-size: 150%;
  text-transform: capitalize;
  margin-right: 0.5rem;
  font-weight: 500;
  margin-bottom: 1rem;
}
.input-field {
  border: none;
  color: var(--font-color);
  border-radius: 5px;
  outline: none;
  padding: 0.5rem;
  width: 3rem;
  font-weight: 600;
  font-size: 110%;
}
p {
  max-width: 1000px;
  width: 90%;
  text-align: center;
  margin: 2rem auto;
}
@media screen and (max-width: 500px) {
  .form-contant {
    flex-wrap: wrap;
  }
  p {
    width: 95%;
  }
}
